.button-container {
  width: 500px;
  height: fit-content;
  flex-direction: row;
  justify-content: center;
  column-gap: 50px;
  margin: 200px auto;
  display: flex;
}

.button-start {
  width: 150px;
  height: 75px;
  background-color: #7bb382;
  border-radius: 25px;
  outline: 2px solid #000;
  margin-right: 30px;
  font-size: 30px;
  box-shadow: 10px 15px 15px rgba(0, 0, 0, .5);
}

.button-stop {
  width: 150px;
  height: 75px;
  background-color: #c46c64;
  border-radius: 25px;
  outline: 2px solid #000;
  font-size: 30px;
  box-shadow: 10px 15px 15px rgba(0, 0, 0, .5);
}

